.Nav {
  display: block;
  padding: 0px;
  width: 100%;
  background-color: white;
  font-weight: 700;
  font-weight: bold;
  font-size: 20px;

  @media screen and (max-width: 700px) {
    position: absolute;
    font-size: 36px;
    line-height: 48px;
    z-index: 90;
    left: 0px;
    top: 0px;
    height: 100vh;
    padding: 50px;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
  }
}
.classShowMobileMenu {
  transform: translateX(0px);
  transition: all 0.5s ease-in-out;
}
.Nav_List {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;

  @media screen and (max-width: 700px) {
    flex-flow: column nowrap;
    background-color: transparent;
  }
}
.Nav__Item {
  list-style: none;
}
.Nav__Item__NavLink {
  text-decoration: none;
  padding: 7px 0 7px 0;
  display: inline-block;
  text-align: center;
  color: black;
  padding: 10px;

  &:hover {
    background-color: #1070ca;
    transition: background-color 0.3s ease-in-out;
  }
}

.active {
  border-bottom: 3px solid #1070ca;
}
